
.list-group-item.individual-item {
    border-radius: .25rem;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}
.list-group-item.individual-item + .list-group-item.individual-item {
    margin-top: 0.5rem;
}
.list-group-item img, .individual-item img {
    max-width: 100px;
    max-height: 50px;
}
.individual-item img {
    border-top-left-radius: .25rem;
    border-bottom-left-radius: .25rem;
}
.multiselect input {
    outline: none;
    font-size: 1rem;
    line-height: 1.25;
    color: #495057;
    background-color: #fff;
    background-image: none;
    background-clip: padding-box;
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    width: calc(100% - 25px);
}
.multiselect input::-webkit-input-placeholder, .multiselect .stub {
    color: rgb(135, 142, 149);
}
.multiselect input:-ms-input-placeholder, .multiselect .stub {
    color: rgb(135, 142, 149);
}
.multiselect input::placeholder, .multiselect .stub {
    color: rgb(135, 142, 149);
}
.multiselect.disabled input {
    background-color: #e9ecef;
}
.multiselect [class^="icon-"]:hover {
    opacity: 0.8;
    cursor: pointer;
}
.multiselect.disabled [class^="icon-"]:hover {
    opacity: 1;
    cursor: default;
}
.multiselect.disabled {
    opacity: 1;
    cursor: default !important;
    outline: none !important;
    border: 1px solid rgba(0, 0, 0, .15);
    background-color: #e9ecef;
}
.dropdown-list {
    z-index: 2000;
    max-height: 250px;
    overflow-y: auto;
    position: absolute;
    -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, .3);
            box-shadow: 0 5px 10px rgba(0, 0, 0, .3);
    border-radius: .25rem;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}
.list-group-item.bg-light {
    cursor: pointer;
}



